export const getCurrentUser = () => {
    const users = JSON.parse(localStorage.getItem('users')) || [];
    const isLoggedInUser = JSON.parse(localStorage.getItem('isLoggedInUser')); //    
    if (!isLoggedInUser) return null; //     null 
    return users.find((user) => user.email === isLoggedInUser) || null;
};

export const saveToLocalStorage = (key, value) => {
    localStorage.setItem(key, JSON.stringify(value));
};

export const getFromLocalStorage = (key, defaultValue = null) => {
    const data = localStorage.getItem(key);
    try {
        return data ? JSON.parse(data) : defaultValue;
    } catch (error) {
        console.error('Invalid JSON format in localStorage:', key, data);
        return defaultValue; // JSON     
    }
};


//     
export const tryLogin = (email, password) => {
    const users = getFromLocalStorage('users', []);
    const user = users.find((u) => u.email === email && u.password === password);
    if (user) {
        saveToLocalStorage('isLoggedInUser', user.email); //    
        return user;
    }
    return null;
};


export const tryRegister = (email, password) => {
    const users = JSON.parse(localStorage.getItem('users')) || [];
    if (users.some(u => u.email === email)) {
        return false;
    }
    users.push({ email, password });
    localStorage.setItem('users', JSON.stringify(users));
    return true;
};

export const isLoggedIn = () => {
    return localStorage.getItem('isLoggedIn') === 'true';
};


export const logout = () => {
    localStorage.removeItem("isLoggedInUser"); //     
    window.location.href = "/signin"; //   /signin 
};
